import React, { Component } from "react";

class HomeData extends Component {
  state = {};

  componentDidMount() {
    document.title = "Xavier Purcell";
  }

  render() {
    return (
      <div>
        '<h1 style={{ color: "white" }}>Home</h1>
        <p
          style={{
            color: "white",
            paddingTop: "50px",
            fontSize: "20px",
            maxWidth: "920px",
            margin: "auto",
          }}
        >
          My name is Xavier Purcell, I created this site to learn Javascript and
          React. Here you can find a few different projects I've created.
        </p>
        <p
          style={{
            color: "white",
            paddingTop: "25px",
            marginBottom: "0px",
            fontSize: "20px",
          }}
        >
          Most projects are proof of concepts rather than polished products.
        </p>
        <p
          style={{
            color: "white",
            paddingTop: "25px",
            marginBottom: "0px",
            fontSize: "20px",
          }}
        >
          Click on the tabs at the top to visit the different projects. Use the
          buttons at the bottom to play around with them.
        </p>
      </div>
    );
  }
}

export default HomeData;
